import React from 'react';
import './Section__Contacts.scss';
import Letter from '../../../assets/contacts/contacts_img.svg';


const SectionContacts = ({contacts}) => {
    return (
        <div className='contacts__wrapper'>
            <div className="contacts__img">
                <img src={Letter} alt="Contacts" className="contacts__pic"/>
            </div>
            <div className="contacts__info_container">
                <h5 className="contacts__name">{contacts.title}</h5>
                <div className='contacts__wrap__info'>
                    <p className="contacts__item">{contacts.email.title}</p>
                    <p className="contacts__info">{contacts.email.info}</p>
                    <p className="contacts__item">{contacts.address.title}</p>
                    <p className="contacts__info">{contacts.address.info}</p>
                    <p className="contacts__item">{contacts.tel.title}</p>
                    <p className="contacts__info">{contacts.tel.info}</p>
                </div>

            </div>
        </div>
    );
};

export default SectionContacts;