import React, {useState, useEffect, useCallback} from 'react'
import './Header.scss'
import Logo from '../Logo/Logo'
import Sidebar from './SideBar/Sidebar'
import NavBar from './NavBar/NavBar'
import LangSwitcher from "./LangSwitcher/LangSwitcher";


const Header = ({
                    logoTypeName,
                    logoSubName,
                    sbTitle1,
                    sbCompanyItems,
                    dropItems,
                    dropTitle2,
                    navBar,
                    dropHeaderMenu
                }) => {
    //changeColor of header function on scroll
    const [headerColor, setHeaderColor] = useState(false);

    const changeBackground = useCallback(() => {
        if (window.scrollY >= 90) {
            setHeaderColor(true);
        } else {
            setHeaderColor(false);
        }
    }, [])

    useEffect(() => {
        window.addEventListener('scroll', changeBackground)
    }, [changeBackground])

    return (
        <div className={headerColor ? 'header__wrap active' : "header__wrap"}>
            <div className="header">
                <Logo
                    logoTypeName={logoTypeName}
                    logoSubName={logoSubName}
                />
                <NavBar
                    navBar={navBar}
                    dropHeaderMenu={dropHeaderMenu}
                />
                <Sidebar
                    logoTypeName={logoTypeName}
                    logoSubName={logoSubName}
                    sbTitle1={sbTitle1}
                    sbCompanyItems={sbCompanyItems}
                    dropTitle2={dropTitle2}
                    dropItems={dropItems}
                />
                <LangSwitcher style={'language'} langStyle/>
            </div>
        </div>
    )
}
export default Header;