import React from 'react';
import './Section__AboutUs.scss';
import MainButton from "../../MainButton/MainButton";

const Section__AboutUs = ({cardsContent, bg, style2}) => {
    return (
        <div className='section__AboutUs_2__wrap'>
        {/*<div className={bg ? 'section__AboutUs_2__wrap2' : 'section__AboutUs_2__wrap'}>*/}
            <div className={style2 ? "section__AboutUs_2__container2" : "section__AboutUs_2__container"}>
                {cardsContent.map((item, index) => {
                    return (
                        <div className={`card_wrap ${item.className}`} key={index}>
                            <div className={`card_img ${item.className}`}>
                                <img src={item.pic} alt="Time_pic" className={`card__pic ${item.className}`}/>
                            </div>
                            <div className="aboutUs__content">
                                <p className={`card__title ${item.className}`}>{item.title}</p>
                                <p className={`card__description ${item.className}`}>{item.description}</p>
                                {item.btn
                                    ?
                                    <MainButton
                                        btnText={item.btn.btnText}
                                        classN={item.btn.classN}
                                        to={item.btn.to}
                                        style2={item.style2}
                                        icon={item.iconBtn}/>
                                    :
                                    null
                                }
                            </div>
                        </div>)
                })}
                    </div>
                    </div>
                    );
                };

export default Section__AboutUs;