import React from 'react';
import Content from "../../Content/Content";
import {useSelector} from "react-redux";
import SectionTitle from "../../components/Sections/Section__Title/Section__Title";
import SectionIntro from "../../components/Sections/Section__Intro/Section__Intro";
import SectionAboutUs from "../../components/Sections/Section__AboutUs/Section__AboutUs";
import SectionStatistic4 from "../../components/Sections/Section__statistic_4/Section__statistic_4";

const MainPage = () => {
    const lng = useSelector(state => state.language.language);

    return (
        <>
            <SectionTitle
                title={Content[lng].section__Title_1.title}
                description={Content[lng].section__Title_1.description}
                pic={Content[lng].section__Title_1.pic}
                btn={Content[lng].section__Title_1.btn}
                className={Content[lng].section__Title_1.className}
                arrowDown={true}
                id={Content[lng].section__Title_1.id}
            />
            <SectionIntro
                title={Content[lng].section__Intro_2.title}
                description={Content[lng].section__Intro_2.description}
            />
            <SectionAboutUs
                cardsContent={Content[lng].section__AboutUs_3}
            />
            <SectionTitle
                title={Content[lng].section__Title_4.title}
                description={Content[lng].section__Title_4.description}
                btn={Content[lng].section__Title_4.btn}
                className={Content[lng].section__Title_4.className}
                style2={"btn__wrap2"}
            />
            <SectionStatistic4
                title={Content[lng].section__Statistic_5.title}
                description={Content[lng].section__Statistic_5.description}
                topics={Content[lng].section__Statistic_5.topicsAll}
            />
            <SectionIntro
                title={Content[lng].section__Intro_6.title}
                description={Content[lng].section__Intro_6.description}
                style2={"intro bigger"}
                id={Content[lng].section__Intro_6.id}
            />
            <SectionAboutUs
                cardsContent={Content[lng].section__OurServices_7}
                // bg={true}
            />
            <SectionTitle
                title={Content[lng].section__Title_8.title}
                btn={Content[lng].section__Title_8.btn}
                className={Content[lng].section__Title_8.className}
                reverseBg={true}
                style2={"btn__wrap3"}
                id={Content[lng].section__Title_8.id}
                name={Content[lng].section__Title_8.name}
            />
        </>

    );
};

export default MainPage;