import React from 'react';
import './Section4.scss';

const Section4 = ({title, subTitle, topicList, className}) => {
    return (
        <div className={className ? `section4__wrapper ${className}` : 'section4__wrapper'}>
            <div className={'section4__container'}>
                <div className="section4__title__wrap">
                    <h1 className={className ? `section4__title ${className}` : "section4__title"}>{title}</h1>
                    <p className={className ? `section4__subTitle ${className}` : "section4__subTitle"}>{subTitle}</p>
                </div>
                <div className="section4__info__wrap">
                    <ul className="section4__info__container">
                        {topicList.map((i, index) => {
                            return (
                                <li className={className ? `section4__list ${className}` : 'section4__list'} key={index}>
                                    {i.title ? <p className="section4__info__title">{i.title}</p> : null}
                                    <p className="section4__info__item">{i.subTitle}</p>
                                </li>
                            )
                        })}
                    </ul>
                </div>
            </div>
        </div>
    );
};

export default Section4;