import React from 'react';
import SectionTitle from "../../components/Sections/Section__Title/Section__Title";
import Section2 from "../../components/Sections/LH/Section2/Section2";
import Content from "../../Content/Content";
import {useSelector} from "react-redux";
import './LiquidityHub.scss';
import Section3 from '../../components/Sections/LH/Section3/Section3'
import Section4 from "../../components/Sections/LH/Section4/Section4";
import FormLHWL from "../../components/Form/FormLHWL";

const LiquidityHub = () => {
    const lng = useSelector(state => state.language.language);

    
    return (
        <>
            <div className="bg__from__start">
            <SectionTitle
                title={Content[lng].liquidityHub.title}
                subTitle={Content[lng].liquidityHub.subTitle}
                subTitleClassName={Content[lng].liquidityHub.subTitleClassName}
                description={Content[lng].liquidityHub.description}
                btn={Content[lng].liquidityHub.btn}
                className={Content[lng].liquidityHub.className}
                arrowDown={true}
                style2={"btnLH__wrap"}
            />
            <Section2
                title={Content[lng].liquidityHub.section2.title}
                titleStyle={Content[lng].liquidityHub.section2.titleStyle}
                pic_1={Content[lng].liquidityHub.section2.pic_1}
                pic_2={Content[lng].liquidityHub.section2.pic_2}
                alt1={Content[lng].liquidityHub.section2.alt1}
                alt2={Content[lng].liquidityHub.section2.alt2}
                description1={Content[lng].liquidityHub.section2.description1}
                description2={Content[lng].liquidityHub.section2.description2}
            />
            <Section3
                title={Content[lng].liquidityHub.section3.title}
                topicList={Content[lng].liquidityHub.section3.topicList}
            />
            </div>
            <Section4
                title={Content[lng].liquidityHub.section4.title}
                subTitle={Content[lng].liquidityHub.section4.subTitle}
                topicList={Content[lng].liquidityHub.section4.topicList}
                className={Content[lng].liquidityHub.className}
            />
            <div className="bg__for__form">
               <FormLHWL form={Content[lng].liquidityHub.formLHWL}/>
            </div>
        </>
    );
};

export default LiquidityHub;