import React from 'react';
import {Formik, Form} from 'formik';
import * as Yup from 'yup';
import {makeStyles} from '@material-ui/core/styles';
import {
    Container,
    Grid,
    Typography
} from '@material-ui/core';
import TextFieldWrapper from './FormComponents/FormTextFields';
import ButtonWrapper from './FormComponents/SubmitButton';


const useStyles = makeStyles((theme) => ({
        formWrapper: {
            marginBottom: '60px',
            ['@media(min-width: 768px)']: { // eslint-disable-line no-useless-computed-key
                marginBottom: '150px',
            },
            ['@media(min-width: 1600px)']: { // eslint-disable-line no-useless-computed-key
                marginBottom: '140px',
            }
        },
        formContainer: {
            background: "#FFFFFF",
            padding: "60px 15px 60px",
            width: "300px",
            boxShadow: "0px 4px 8px rgba(16, 101, 152, 0.16)",
            borderRadius: "30px",
            ['@media(min-width: 768px)']: { // eslint-disable-line no-useless-computed-key
                width: "600px",
                padding: "60px 128px",
            },
            ['@media(min-width: 1600px)']: { // eslint-disable-line no-useless-computed-key
                width: "664px",
                padding: "60px 160px",
            }
        },
        title: {
            fontWeight: "bold",
            fontSize: "30px",
            lineHeight: "28px",
            textAlign: "center",
            color: "#002459",
            margin: "0 0 30px",
            ['@media(min-width: 768px)']: { // eslint-disable-line no-useless-computed-key
                fontWeight: "700",
                fontSize: "36px",
            },
},
        label: {
            fontWeight: "600",
            fontSize: "16px",
            lineHeight: "24px",
            color: "#002459",
            marginBottom: "5px",
        },
        input: {
            height: "75px",
            marginBottom: "11px",
            "& input": {
                padding: "17.5px 14px",
                ['@media(min-width: 768px)']: { // eslint-disable-line no-useless-computed-key
                    padding: "17.5px 14px",
                },
            },
            "& input::placeholder": {
                fontWeight: "400",
                fontSize: "16px",
                lineHeight: "24px",
                color: '#6B778C',
            },
        },
        inputMultiline: {
            height: "151px",
            marginBottom: "30px",
            "& > div" : {
                ['@media(min-width: 1600px)']: { // eslint-disable-line no-useless-computed-key
                    height: '160px'
                },
            },

            "& input": {
                padding: "18.2px 14px",
            },
            "& textarea::placeholder": {
                fontWeight: "400",
                fontSize: "16px",
                lineHeight: "24px",
                color: '#6B778C',
            },
            ['@media(min-width: 1600px)']: { // eslint-disable-line no-useless-computed-key
                height: "160px",
            },
        },
        submitBtn: {
            width: "138px",
            height: "48px",
            background: "linear-gradient(268.11deg, #1082C2 1.6%, #31A7EA 98.4%, #31A7EA 98.4%)",
            borderRadius: "10px",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            margin: "0 auto 0",

            "& > span": {
                color: "white",
                textDecoration: "none",
                fontSize: "14px",
                fontWeight: "600",
                lineHeight: "19px",
                textTransform: "capitalize",

                "&:hover": {
                    color: "#1E92D4",
                }
            },
            "&:hover": {
                background: "transparent",
                border: "1px solid #1E92D4",
                cursor: "pointer",
                color: "#1E92D4",

                "& > span": {
                    color: "#1E92D4",
                }
            }
        }
    }))
;

const INITIAL_FORM_STATE = {
    name: '',
    email: '',
    message: '',
};

const FORM_VALIDATION = Yup.object().shape({
    name: Yup.string()
        .min(3, 'Please enter a minimum 3 letters.')
        .required('Required'),

    email: Yup.string()
        .email('Please enter a valid email.')
        .required('Required'),
    message: Yup.string(),
});


const FormContacts = ({form}) => {
    const classes = useStyles();
    return (
        <div className={classes.formWrapper}>
            <Container className={classes.formContainer}>
                <Formik
                    initialValues={{
                        ...INITIAL_FORM_STATE
                    }}
                    validationSchema={FORM_VALIDATION}
                    onSubmit={values => {
                        // console.log(values);
                    }}
                >
                    <Form>
                        <Grid item xs={12}>
                            <Typography className={classes.title}>
                                {form.title}
                            </Typography>
                        </Grid>

                        <Grid container>
                            <Grid item xs={12}>
                                <Typography className={classes.label}>
                                    {form.name.label}
                                </Typography>
                            </Grid>
                            <Grid item xs={12}>
                                <TextFieldWrapper
                                    name="name"
                                    placeholder={form.name.placeholder}
                                    className={classes.input}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <Typography className={classes.label}>
                                    {form.email.label}
                                </Typography>
                            </Grid>
                            <Grid item xs={12}>
                                <TextFieldWrapper
                                    name="email"
                                    placeholder={form.email.placeholder}
                                    className={classes.input}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <Typography className={classes.label}>
                                    {form.message.label}
                                </Typography>
                            </Grid>
                            <Grid item xs={12}>
                                <TextFieldWrapper
                                    name="message"
                                    placeholder={form.message.placeholder}
                                    multiline={true}
                                    rows={6}
                                    className={classes.inputMultiline}
                                />
                            </Grid>
                        </Grid>
                        <Grid item xs={12}>
                            <ButtonWrapper className={classes.submitBtn}>
                                {form.btnText}
                            </ButtonWrapper>
                        </Grid>
                    </Form>
                </Formik>
            </Container>
        </div>
    );
};


export default FormContacts;