
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import LanguageIcon from '@material-ui/icons/Language';
import './LangSwitcher.scss';
import { saveLanguage } from '../../../redux/language/languageActions';
import {connect, useDispatch,useSelector} from 'react-redux';

import Content from '../../../Content/Content'


const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 50,
    },
    input1Style:{
        color:'white'
    },
    input2Style: {
        //size of RU input which we can see
        color: '#527497',
        fontSize: '16px',
    },
    icon: {
        color: '#527497',
        fontSize: "18px",
        margin: "3px 10px 0 0",
    },
    icon2style: {
        color: '#527497',
        fontSize: "16px",
        margin: "3px 16px 0 0",
    },
    dropDownMenu : {
        color: '#527497',
        background: "linear-gradient(180.6deg, #E7F7FD 100%, rgba(231, 247, 253, 0.572544) 32.94%, rgba(231, 247, 253, 0) 76.37%)"
        // width: "70px",
    }
}));



const LangSwitcher = ({style,language, selectStyle, colorLang, langStyle})=> {
    
    const lng = useSelector(state => state.language.language)

    const dispatch = useDispatch();
    const classes = useStyles();

    const handleChange = (event) => {
        dispatch(saveLanguage(event.target.value))
    };

    return (
        <div className = {style === 'language' ? 'language' : 'mobile'}>
            {style !== 'language' ? <p className={'lang__switch__title'}>{Content[lng].header.langSwitcher.chooseLanguage}:</p> : null}
            <div className="lang__container">
            <LanguageIcon
                style={ langStyle
                    ?
                    {fontSize: '18px', color: '#527497', marginRight:'-5px'}
                    :
                    // works only if pass styles straight
                    {fontSize: '20px', color: '#527497', marginRight:'-5px'}}
            />
            <FormControl className={classes.formControl}>
                <Select
                    defaultValue={"RU"}
                    className={selectStyle ? classes.input1Style : classes.input2Style}
                    disableUnderline
                    value={language}
                    onChange={handleChange}
                    IconComponent={ExpandMoreIcon}
                    inputProps={{
                        classes: {
                            // input: selectStyle ? classes.input1Style : classes.input2Style,
                            icon: langStyle ? classes.icon2style :  classes.icon
                        }
                    }}
                    MenuProps={{ classes: { paper: classes.dropDownMenu },
                        variant: 'menu',
                        disableScrollLock: true,
                        //setting variant to menu makes it appear below the element
                    }}
                >
                    <MenuItem value={"RU"} style={{color:`${colorLang}`}}><span className="input__value">RU</span></MenuItem>
                    <MenuItem value={"EN"} style={{color:`${colorLang}`}}><span className="input__value">EN</span></MenuItem>
                </Select>
            </FormControl>
            </div>
        </div>
    );
}



const mapStateToProps = (state) => {
    return{
        language: state.language.language
    }
}

export default  connect(mapStateToProps)(LangSwitcher);



