import React, {useEffect, useState} from 'react'
import './NavBar.scss'
import {Link as LinkR} from 'react-router-dom';
import DropDownMenu from "../DropDownMenu/DropDownMenu";
import ScrollTop2 from "../../ScrollTop2/ScrollToTop2"

const NavBar = ({navBar, dropHeaderMenu}) => {
    const [dropDown, setDropDown] = useState(false);
    const onMouseEnter = () => (window.innerWidth <= 1600) ? setDropDown(false) : setDropDown(true)
    const onMouseLeave = () => (window.innerWidth < 1600) ? setDropDown(false) : setDropDown(false)
    console.log(window.location.pathname);
    const checkLocation = () => {
        return window.location.pathname;
    }

    useEffect( () => {
        checkLocation()
    }, [checkLocation])

    return (
        (checkLocation() === "/fin-tech-kate-test/"
        || checkLocation() === "/fin-tech-kate-test/#main"
        || checkLocation() === "/fin-tech-kate-test/#aboutus") ?
                <nav className='navBar__header'>
                    <ul className='navBar__nav'>
                        <li className="navBar__header__li">
                            <a className='navBar__item' href="/fin-tech-kate-test/#main">{navBar.main}</a>
                        </li>
                        <li className="navBar__header__li">
                            <a className='navBar__item' href="/fin-tech-kate-test/#aboutus">
                                {navBar.aboutUs}
                            </a>
                        </li>
                        <li className="navBar__header__li drop_li" onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave}>
                            <a className='navBar__item' href="/fin-tech-kate-test/#products">
                                {navBar.products}
                                <i className="fas fa-chevron-down"/>
                                {dropDown && <DropDownMenu dropHeaderMenu={dropHeaderMenu}/>}
                            </a>
                        </li>
                        <li className="navBar__header__li">
                            <LinkR className='navBar__item' to="/contacts">
                                {navBar.contacts}
                            </LinkR>
                        </li>
                    </ul>
                </nav>
                :
        <nav className='navBar__header'>
            <ul className='navBar__nav'>
                <li className="navBar__header__li">
                    <LinkR className='navBar__item' to="/fin-tech-kate-test/#main">{navBar.main}</LinkR>
                    </li>
                    <li className="navBar__header__li">
                    <LinkR className='navBar__item' to="/fin-tech-kate-test/#aboutus" onClick={() => <ScrollTop2 />}>
                {navBar.aboutUs}
                    </LinkR>
                    </li>
                    <li className="navBar__header__li drop_li" onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave}>
                    <LinkR className='navBar__item' to="/fin-tech-kate-test/#products">
                {navBar.products}
                    <i className="fas fa-chevron-down"/>
                {dropDown && <DropDownMenu dropHeaderMenu={dropHeaderMenu}/>}
                    </LinkR>
                    </li>
                <li className="navBar__header__li">
                    <LinkR className='navBar__item' to="/contacts">
                        {navBar.contacts}
                    </LinkR>
                </li>
            </ul>
        </nav>

    )
}
export default NavBar
