import React from 'react';
import {Formik, Form} from 'formik';
import * as Yup from 'yup';
import {makeStyles} from '@material-ui/core/styles';
import {
    Container,
    Grid,
    Typography
} from '@material-ui/core';
import TextFieldWrapper from './FormComponents/FormTextFields';
import ButtonWrapper from './FormComponents/SubmitButton';


const useStyles = makeStyles((theme) => ({
        formWrapper: {
            paddingBottom: "120px",
            ['@media(min-width: 768px)']: { // eslint-disable-line no-useless-computed-key
                // width: "600px",
                paddingBottom: "141px",
                // background: "linear-gradient(0deg, #E7F7FD 0.51%, rgba(231, 247, 253, 0.572544) 32.94%, rgba(231, 247, 253, 0) 76.37%)",
            },
            ['@media(min-width: 1600px)']: { // eslint-disable-line no-useless-computed-key
                paddingBottom: '150px',
            }
        },
        form: {
            ['@media(min-width: 1600px)']: { // eslint-disable-line no-useless-computed-key
                display: 'grid',
                gridTemplateColumns: "1fr auto",
            }
        },
        formContainer: {
            background: "#FFFFFF",
            padding: "60px 15px 60px",
            width: "300px",
            boxShadow: "0px 4px 8px rgba(16, 101, 152, 0.16)",
            borderRadius: "30px",
            ['@media(min-width: 768px)']: { // eslint-disable-line no-useless-computed-key
                width: "600px",
                padding: "60px 130px",
            },
            ['@media(min-width: 1600px)']: { // eslint-disable-line no-useless-computed-key
                width: "1198px",
                height: "203px",
                margin: "0 auto",
                padding: "60px 110px",
            }
        },
        label: {
            fontWeight: "600",
            fontSize: "16px",
            lineHeight: "24px",
            color: "#002459",
            marginBottom: "5px",
        },
        titleContainer: {
            width: "300px",
            margin: "0 auto",
            ['@media(min-width: 768px)']: { // eslint-disable-line no-useless-computed-key
                width: "600px",
            },
            ['@media(min-width: 1600px)']: { // eslint-disable-line no-useless-computed-key
                width: "1200px",
            },
        },
        title: {
            // textTransform: "uppercase",
            fontWeight: "700",
            fontSize: "24px",
            lineHeight: "36px",
            color: "#002459",
            textAlign: "center",
            marginBottom: "30px",
            padding: "0 20px 0",
            // padding: "120px 20px 0",
            ['@media(min-width: 768px)']: { // eslint-disable-line no-useless-computed-key
                textTransform: "none",
                fontSize: "36px",
                lineHeight: "54px",
                marginBottom: "30px",
            },
            ['@media(min-width: 1600px)']: { // eslint-disable-line no-useless-computed-key
                padding: "0 110px 0",
                margin: "0 auto 30px",
            },
        },
        subTitle: {
            fontWeight: "400",
            fontSize: "18px",
            lineHeight: "27px",
            color: "#002459",
            textAlign: "center",
            display: "block",
            margin: "0 auto 30px",
            padding: "0 20px 0",
            ['@media(min-width: 1600px)']: { // eslint-disable-line no-useless-computed-key
                margin: "0 auto 60px",
            },
        },
        inputContainer: {
            ['@media(min-width: 1600px)']: { // eslint-disable-line no-useless-computed-key
                display: "grid",
                gridTemplateColumns: "250px 250px 250px",
                gridColumnGap: "30px",
                alignItems: "center",
            },
        },
        input: {
            height: "75px",
            marginBottom: "11px",
            "& input": {
                padding: "17.5px 14px",
                ['@media(min-width: 768px)']: { // eslint-disable-line no-useless-computed-key
                    padding: "17.5px 14px",
                },
            },
            "& input::placeholder": {
                fontWeight: "400",
                fontSize: "16px",
                lineHeight: "24px",
                color: '#6B778C',
            },
        },
        submitBtnContainer: {
            alignSelf: "center",
        },
        submitBtn: {
            width: "138px",
            height: "48px",
            background: "linear-gradient(268.11deg, #1082C2 1.6%, #31A7EA 98.4%, #31A7EA 98.4%)",
            borderRadius: "10px",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            margin: "0 auto 0",

            "& > span": {
                color: "white",
                textDecoration: "none",
                fontSize: "14px",
                fontWeight: "600",
                lineHeight: "19px",
                textTransform: "capitalize",
            },
            "&:hover": {
                background: "transparent",
                border: "1px solid #1E92D4",
                cursor: "pointer",
                color: "#1E92D4",

                "& > span": {
                    color: "#1E92D4",
                }
            }
        }
    }))
;

const INITIAL_FORM_STATE = {
    name: '',
    email: '',
    phone: '',
};

const phoneRegex = /^((\+\d{1,3}(-| )?\(?\d\)?(-| )?\d{1,5})|(\(?\d{2,6}\)?))(-| )?(\d{3,4})(-| )?(\d{4})(( x| ext)\d{1,5}){0,1}$/;

const FORM_VALIDATION = Yup.object().shape({
    name: Yup.string()
        .min(3, 'Please enter a minimum 3 letters.')
        .required('Required'),

    email: Yup.string()
        .email('Please enter a valid email.')
        .required('Required'),
    phone: Yup.string().matches(phoneRegex, "Invalid phone, please try again").required("Required")
});


const FormContacts = ({form}) => {
    const classes = useStyles();

    return (
        <div className={classes.formWrapper}>
            <Grid container className={classes.titleContainer}>
                <Typography className={classes.title}>
                    {form.title}
                </Typography>
                {form.subTitle ?
                    <Typography className={classes.subTitle}>
                        {form.subTitle}
                    </Typography> : null}
            </Grid>
            <Container className={classes.formContainer}>
                <Formik
                    initialValues={{
                        ...INITIAL_FORM_STATE
                    }}
                    validationSchema={FORM_VALIDATION}
                    onSubmit={values => {
                        // console.log(values);
                    }}
                >
                    <Form className={classes.form}>
                        <Grid container className={classes.inputContainer}>
                            <Grid item xs={12}>
                                <Typography className={classes.label}>
                                    {form.name.label}
                                </Typography>
                                <TextFieldWrapper
                                    name="name"
                                    placeholder={form.name.placeholder}
                                    className={classes.input}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <Typography className={classes.label}>
                                    {form.email.label}
                                </Typography>
                                <TextFieldWrapper
                                    name="email"
                                    placeholder={form.email.placeholder}
                                    className={classes.input}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <Typography className={classes.label}>
                                    {form.phone.label}
                                </Typography>
                                <TextFieldWrapper
                                    name="phone"
                                    placeholder={form.phone.placeholder}
                                    className={classes.input}
                                />
                            </Grid>
                        </Grid>
                        <Grid item xs={12} className={classes.submitBtnContainer}>
                            <ButtonWrapper className={classes.submitBtn}>
                                {form.btnText}
                            </ButtonWrapper>
                        </Grid>
                    </Form>
                </Formik>
            </Container>
        </div>
    );
};


export default FormContacts;