import React from 'react';
import './TitlePicture.scss';

const TitlePicture = ({className, pic, picAlt }) => {
    return (
        <div className={`${className}__pic__wrap`}>
                <img src={pic} alt={picAlt} className={`${className}__pic`}/>
        </div>
    );
};

export default TitlePicture;