import React, {useState} from 'react'
import Logo from '../../Logo/Logo'
import './Sidebar.scss'
import {Link as LinkR} from 'react-router-dom';
import {slide as Menu} from 'react-burger-menu'
import LangSwitcher from '../LangSwitcher/LangSwitcher'
import ScrollTop2 from "../../ScrollTop2/ScrollToTop2";

const Sidebar = ({
                     logoSubName,
                     logoTypeName,
                     sbTitle1,
                     sbCompanyItems,
                     dropTitle2,
                     dropItems
                 }) => {
    const [menuIsOpen, setMenuIsOpen] = useState(false);
    const closeMenu = () => setMenuIsOpen(!menuIsOpen);

    return (
        <div id="outer-container">
            <Menu
                left
                pageWrapId={"page-wrap"}
                outerContainerId={"outer-container"}
                width={'320px'}
                // for width burger bars
                burgerBarClassName={"burger__bars"}
                isOpen={menuIsOpen}
                onOpen={closeMenu}
            >
                <Logo
                    logoTypeName={logoTypeName}
                    logoSubName={logoSubName}
                />
                {/*<ul className="sb__list1__title">*/}
                {/*    {sbTitle1 ? <li className="sb__Title">{sbTitle1}</li> : null}*/}
                {/*    {sbCompanyItems.map((item, index) =>*/}
                {/*        <li key={index} className={'sbList'}>*/}
                {/*            <a*/}
                {/*                className='menu_item'*/}
                {/*                href={item.url}*/}
                {/*                onClick={() => closeMenu()}*/}
                {/*            >*/}
                {/*                {item.title}*/}
                {/*            </a>*/}
                {/*        </li>*/}
                {/*    )}*/}
                {/*</ul>*/}
                <ul className="sb__list1__title">
                    {sbTitle1 ? <li className="sb__Title">{sbTitle1}</li> : null}

                    {
                        (window.location.pathname === "/liquidityhub"
                            || window.location.pathname === "/whitelabel"
                            || window.location.pathname === "/contacts")
                            ?
<>
                            <li className={'sbList'}>
                                <LinkR
                                    className='menu_item'
                                    to={sbCompanyItems.url1}
                                    onClick={() => closeMenu()}
                                >
                                    {sbCompanyItems.title1}
                                </LinkR>
                            </li>
                            <li className={'sbList'}>
                                <LinkR
                                    className='menu_item'
                                    to={sbCompanyItems.url2}
                                    onClick={() => closeMenu()}
                                >
                                    {sbCompanyItems.title2}
                                </LinkR>
                            </li>
                        <li  className={'sbList'}>
                        <LinkR
                        className='menu_item'
                        to={sbCompanyItems.url3}
                        onClick={() => closeMenu()}
                        >
                    {sbCompanyItems.title3}
                        </LinkR>
                        </li>
                            </>
                        :
                            <>
                        <li  className={'sbList'}>
                        <a
                        className='menu_item'
                        href={sbCompanyItems.url1}
                        onClick={() => closeMenu()}
                        >
                    {sbCompanyItems.title1}
                        </a>
                        </li>
                        <li  className={'sbList'}>
                        <a
                        className='menu_item'
                        href={sbCompanyItems.url2}
                        onClick={() => closeMenu()}
                        >
                    {sbCompanyItems.title2}
                        </a>
                        </li>
                                <li  className={'sbList'}>
                                    <a
                                        className='menu_item'
                                        href={sbCompanyItems.url4}
                                        onClick={() => closeMenu()}
                                    >
                                        {sbCompanyItems.title4}
                                    </a>
                                </li>
                        <li  className={'sbList'}>
                        <LinkR
                        className='menu_item'
                        to={sbCompanyItems.url3}
                        onClick={() => closeMenu()}
                        >
                    {sbCompanyItems.title3}
                        </LinkR>
                        </li>
                        </>
                    }
                </ul>
                {/*<ul className="sb__list2__title">*/}
                {/*    {dropTitle2 ? <li className="sb__Title">{dropTitle2}</li> : null}*/}
                {/*    {dropItems.map((item, index) =>*/}
                {/*        <li key={index} className={'sbList'}>*/}
                {/*            <a*/}
                {/*                className='menu_item'*/}
                {/*                href={item.url}*/}
                {/*            >*/}
                {/*                {item.title}*/}
                {/*            </a>*/}
                {/*        </li>*/}
                {/*    )}*/}
                <ul>
                    <li className={'sbList'}>
                        <LinkR
                            className='menu_item'
                            to={dropItems.url1}
                            onClick={() => closeMenu()}
                        >
                            {dropItems.title1}
                        </LinkR>
                    </li>
                    <li className={'sbList'}>
                        <LinkR
                            className='menu_item'
                            to={dropItems.url2}
                            onClick={() => closeMenu()}
                        >
                            {dropItems.title2}
                        </LinkR>
                    </li>
                </ul>
                <LangSwitcher style='language'/>
            </Menu>
        </div>

    )
}
export default Sidebar