import React from 'react';
import './Section__statistic_4.scss'

const Section__statistic_4 = ({title, description, topics}) => {
    return (
        <div className="sectionStatistic__4_wrap">
            <div className="sectionStatistic__4_container">
                <div className="statistic__title_wrap">
                    <h3 className="statistic__title">{title}</h3>
                    <p className="statistic__description">{description}</p>
                </div>
                <div className="statistic__topics_wrap">
                    {topics.map((item, index) => {
                        return (
                                <div className="statistic" key={index}>
                                    <span className="statistic__numbers">{item.statistic}</span>
                                    <p className="statistic__topic">{item.topic}</p>
                                </div>
                        )
                    })}
                </div>
            </div>
        </div>
    );
};

export default Section__statistic_4;