import React from 'react';
import './Footer.scss';
import Logo from '../Logo/Logo'
import SubNavFooter from "./SubNavFooter/SubNavFooter";


const Footer = ({
                    logoSubName,
                    logoTypeName,
                    navigation,
                    footerContacts,
                    footerRights,
                }) => {
    return (
        <div className={"footer__wrap"}>
            <div className="footer__container">
                <Logo
                    logoSubName={logoSubName}
                    logoTypeName={logoTypeName}
                    logoTypeNameStyle={'footer__logo__mainName'}
                    logoNameStyle={'footer__logo__name'}
                    logoTypeStyle={'footer__logo__type'}
                    containerStyle={'footer__logo__container'}
                    logoNameContainerStyle={'footer__logo__name__wrap'}
                />
                <div className="footer__nav">
                    {navigation.map((item, index) => {
                        return <SubNavFooter item={item} key={index}/>;
                    })}
                </div>
                <div className="footer__contacts">
                    <p className="contacts__title">{footerContacts.title}</p>
                    <p className="contacts__email">{footerContacts.email}</p>
                    <p className="contacts__phone">{footerContacts.phone}</p>
                </div>
                <div className="footer__rights">
                    <div className="contacts__copy">
                        <p>{'2018-' + new Date().getFullYear() + " "} &copy;</p>
                        <span className='footer__copy'>{footerRights.string}</span>
                    </div>
                    <p className="contacts__rights">{footerRights.subString}</p>
                </div>
            </div>
        </div>
    );
};

export default Footer;