import React from 'react';
import './Section3.scss'


const Section3 = ({
                      wrapStyle,
                      titleStyle,
                      title,
                      topicList,
                      id
                  }) => {
    return (
        <div className={wrapStyle ? wrapStyle : "section3__wrap"} id={id}>
                    <h2 className={titleStyle ? titleStyle : "section3__title"}>{title}</h2>
                <div className="section3_items_wrap">
                    {topicList ?
                        topicList.map((i, index) => {
                        return (
                            <div className="section3_items" key={index}>
                                <img src={i.pic} alt="Opportunity" className="item_pic"/>
                                <div className="item_description">{i.description}</div>
                            </div>
                        )
                    })
                        :
                        null
                    }
                </div>
        </div>
    );
};

export default Section3;