import React from 'react'
import {Route, Switch, Redirect} from 'react-router-dom';
import MainPage from '../pages/MainPage/MainPage';
import NotFound from '../pages/NotFound/NotFound';
import Header from '../components/Header/Header'
import Contacts from '../pages/Contacts/Contacts'
import {useSelector} from "react-redux";
import Content from "../Content/Content";
import Footer from "../components/Footer/Footer";
import LiquidityHub from "../pages/LiquidityHub/LiquidityHub";
import WhiteLabel from "../pages/WhiteLabel/WhiteLabel";
import ScrollToTop from "../components/ScrollToTop/ScrollToTop";


const MainRoutes = () => {
    const lng = useSelector(state => state.language.language);
    return (
        <>
            <Header
                logoSubName={Content[lng].logo.logoSubName}
                logoTypeName={Content[lng].logo.logoTypeName}
                sbTitle1={Content[lng].sideBar.navBar.title}
                sbCompanyItems={Content[lng].sideBar.navBar.items}
                dropTitle2={Content[lng].sideBar.dropdownMenu.title}
                dropItems={Content[lng].sideBar.dropdownMenu.items}
                navBar={Content[lng].header.navBar}
                dropHeaderMenu={Content[lng].header.dropdownMenu}
            />
            <ScrollToTop />
            <Switch>
                <Route exact path ='/fin-tech-kate-test/' component={MainPage}/>
                <Route exact path ='/contacts' component={Contacts}/>
                <Route exact path ='/liquidityhub' component={LiquidityHub}/>
                <Route exact path ='/whiteLabel' component={WhiteLabel}/>
                <Route exact path ='*' component={NotFound}/>
                {/*<Route exact path ='*' component={NotFound}/>*/}
                <Redirect from="/#main" to={"/fin-tech-kate-test/"}/>
                <Redirect from="/#aboutus" to={"/fin-tech-kate-test/#aboutus"}/>
            </Switch>
            <Footer
                logoSubName={Content[lng].logo.logoSubName}
                logoTypeName={Content[lng].logo.logoTypeName}
                navigation={Content[lng].footer.nav}
                footerContacts={Content[lng].footer.footerContacts}
                footerRights={Content[lng].footer.footerRights}
            />
        </>
    )
}
export default MainRoutes;