import React from 'react';
import './Section2.scss';

const Section2 = ({pic_1, pic_2, description1, description2, title, alt1, alt2, className}) => {
    return (
        <div className='section2__wrap'>
            <div className={className ? `section2__container ${className}` : "section2__container"}>
                {title ? <h4 className="section2__title">{title}</h4> : null}
                <div className={className ? `section2__topic1 ${className}` : "section2__topic1"}>
                    <div className={className ? `topic1__wrap ${className}` : 'topic1__wrap'}>
                        <img src={pic_1} alt={alt1}
                             className={className ? `section1__pic ${className}` : "section1__pic"}/>
                    </div>
                    <div className={className ? `topic__item__container ${className}` : "topic__item__container"}>
                        <p className={className ? `topic__item ${className}` : "topic__item"}>{description1}</p>
                    </div>
                </div>
                <div className={className ? `section2__topic1 ${className}` : "section2__topic1"}>
                    <div className={className ? `topic2__wrap ${className}` : 'topic2__wrap'}>
                        <img src={pic_2} alt={alt2}
                             className={className ? `section2__pic ${className}` : "section2__pic"}/>
                    </div>
                    <div className={className ? `topic__item__container ${className}` : "topic__item__container"}>
                        <p className={className ? `topic__item ${className}` : "topic__item"}>{description2}</p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Section2;