import React from 'react'
import Logotype from '../../assets/logo/logo.png'
import './Logo.scss'
import {Link as LinkS} from "react-scroll";
// import {Link as LinkS} from "react-router-dom";

const Logo = ({
                  src,
                  onClick,
                  logoTypeStyle,
                  logoNameStyle,
                  logoTypeNameStyle,
                  logoTypeName,
                  logoSubName,
                  containerStyle,
                  logoNameContainerStyle
              }) => {
    return (
        <div className={containerStyle ? containerStyle : 'logo__container'}>
            <div className="logo__type__wrap">
                <LinkS to="main" className="logo" onClick={onClick}>
                    <img
                        src={!src ? Logotype : src}
                        alt="Logo-pic"
                        className={logoTypeStyle ? logoTypeStyle : 'logo__type'}
                    />
                </LinkS>
            </div>
            <div className={logoNameContainerStyle ? logoNameContainerStyle :"logo__name__wrap"}>
                <LinkS to="main" className="logo__link" onClick={onClick}>
                    <h3 className={logoTypeNameStyle ? logoTypeNameStyle : "logo__mainName"}>{logoTypeName}</h3>
                </LinkS>
                <LinkS to="main" className="logo__link" onClick={onClick}>
                    <p className={logoNameStyle ? logoNameStyle : "logo__name"}>{logoSubName}</p>
                </LinkS>
            </div>
        </div>
    )
}
export default Logo;