import React from 'react';
import Content from "../../Content/Content";
import {useSelector} from "react-redux";
import './WhiteLabel.scss';
import SectionTitle from "../../components/Sections/Section__Title/Section__Title";
import Section2 from "../../components/Sections/LH/Section2/Section2";
import FormLHWL from "../../components/Form/FormLHWL";
import SectionIntro from "../../components/Sections/Section__Intro/Section__Intro";
import SectionAboutUs from "../../components/Sections/Section__AboutUs/Section__AboutUs";
import Section4 from "../../components/Sections/LH/Section4/Section4";
import Section5 from "../../components/Sections/LH/Section5/Section5";

const WhiteLabel = () => {
    const lng = useSelector(state => state.language.language);

    return (
        <>
            <div className="bg__from__start">
            <SectionTitle
                title={Content[lng].whiteLabel.title}
                pic={Content[lng].whiteLabel.pic}
                subTitle1={Content[lng].whiteLabel.subTitle1}
                subTitle2={Content[lng].whiteLabel.subTitle2}
                subTitle3={Content[lng].whiteLabel.subTitle3}
                subTitleClassName={Content[lng].whiteLabel.subTitleClassName}
                description={Content[lng].whiteLabel.description}
                btn={Content[lng].whiteLabel.btn}
                className={Content[lng].whiteLabel.className}
                arrowDown={true}
                style2={"btnWL__wrap"}
                typed={true}
            />
            <SectionIntro
                title={Content[lng].whiteLabel.sectionIntro}
            />
            <Section2
                pic_1={Content[lng].whiteLabel.section2.pic_1}
                pic_2={Content[lng].whiteLabel.section2.pic_2}
                alt1={Content[lng].whiteLabel.section2.alt1}
                alt2={Content[lng].whiteLabel.section2.alt2}
                description1={Content[lng].whiteLabel.section2.description1}
                description2={Content[lng].whiteLabel.section2.description2}
                className={Content[lng].whiteLabel.className}
            />
            <SectionIntro
                title={Content[lng].whiteLabel.sectionIntro2}
                style2={"intro WL"}
            />
            <SectionAboutUs
                cardsContent={Content[lng].whiteLabel.section3}
                style2={true}
            />
            </div>
            <Section4
                title={Content[lng].whiteLabel.section4.title}
                subTitle={Content[lng].whiteLabel.section4.subTitle}
                topicList={Content[lng].whiteLabel.section4.topicList}
                className={Content[lng].whiteLabel.section4.className}
            />
            <div className="bg__for__form">
            <Section5
                title={Content[lng].whiteLabel.section5.title}
                stepsList={Content[lng].whiteLabel.section5.stepsList}
            />
            <FormLHWL
                form={Content[lng].whiteLabel.formLHWL}
            />
            </div>
        </>
    );
};

export default WhiteLabel;