import React from 'react';
import Content from "../../Content/Content";
import {useSelector} from "react-redux";
import './NotFound.scss'
import SectionTitle from "../../components/Sections/Section__Title/Section__Title";


const NotFound = () => {

    const lng = useSelector(state => state.language.language);

    return (
        <>
            <div className="bg__from__start">
                <SectionTitle
                    title={Content[lng].section404.title}
                    pic={Content[lng].section404.pic}
                    subTitle={Content[lng].section404.subTitle}
                    subTitleClassName={Content[lng].section404.subTitleClassName}
                    description={Content[lng].section404.description}
                    btn={Content[lng].section404.btn}
                    className={Content[lng].section404.className}
                    // style2={"btnLH__wrap"}
                />
            </div>
        </>
    );
};

export default NotFound;