import React from 'react';
import Arrow from '../../assets/other/ArrowsDown.svg';
import './ArrowsDown.scss'

const ArrowsDown = () => {
    return (
        <>
        <div className='arrows__wrap' id={"aboutus"}>
            <img src={Arrow} alt="arrow_down first" className="arrows__down"/>
            <img src={Arrow} alt="arrow_down" className="arrows__down"/>
        </div>
        </>
    );
};

export default ArrowsDown;