import React from 'react';
import './Section5.scss';
import Steps from '../../../../assets/wl/Steps.svg';

const Section5 = ({title, stepsList}) => {
    return (
        <div className='section5__wrap'>
            <div className="section5__container">
                <h4 className="section5__title">{title}</h4>
                <div className="section5__steps__container">
                    {stepsList.map((i) => {
                        return (
                            <div className="step__wrap" key={i.stepNumber}>
                                <p className="step">{i.step}</p>
                                <p className="step_count">{i.stepNumber}</p>
                                <p className="step_name">{i.stepName}</p>
                                <p className="step_description">{i.stepDescription}</p>
                            </div>
                        )
                    })}
                </div>
                    <div className="step__pic">
                        <img src={Steps} alt="Steps" className="section5__pic"/>
                    </div>
            </div>
        </div>
    );
};

export default Section5;