import React from 'react';
import "./Section__Intro.scss";

const SectionIntro = ({title, description, style2, id}) => {
    return (
        // <div className='intro__wrap' id={id} >
            <div className={style2 ? `intro__wrap_2` : 'intro__wrap'} id={id} >
            <div className="intro__container">
                <span className="separator"/>
                <h3 className={style2 ? style2 : "intro"}>{title}</h3>
                <span className="separator"/>
            </div>
            {description ? <p className="intro__description">{description}</p> : null}
        </div>
    );
};

export default SectionIntro;