import React from 'react';
import MainButton from "../../MainButton/MainButton";
import './Section__Title.scss';
import './Section__Title_3.scss';
import ArrowsDown from "../../ArrowsDown/ArrowsDown";
import TitlePicture from "../../TitlePicture/TitlePicture";
import Typed from 'react-typed';


const Section__Title = ({
                            title,
                            description,
                            btn,
                            className,
                            pic,
                            arrowDown,
                            style2,
                            reverseBg,
                            id,
                            subTitle,
                            subTitle1,
                            subTitle2,
                            subTitle3,
                            subTitleClassName,
                            typed

}) => {
    return (
        <div className={ reverseBg ? `${className}__wrap2` : `${className}__wrap`} id={id} >
            <div className={reverseBg ? `${className}__container2` : `${className}__container`}>
                { pic ? <TitlePicture className={className} pic={pic} picAlt={'digital'}/> : null}
                <div className={`${className}__content`}>
                    {( !typed && subTitle1 && subTitle2 && subTitle3) ?
                        <>
                        <h3 className={subTitleClassName}>{subTitle1}<span>{{subTitle2}}</span> <span>{{subTitle3}}</span></h3>
                        </>
                        : <h3 className={subTitleClassName}>{subTitle}</h3>}
                    {(typed && subTitle1 && subTitle2 && subTitle3) ?
                        <>
                            <h3 className={subTitleClassName}>{subTitle1}</h3>
                            <Typed className={subTitleClassName}
                                strings={[`${subTitle2}`,`${subTitle3}`]}
                                typeSpeed={80}
                                backSpeed={60}
                                loop
                            />
                        </>
                        : null}
                    {title.first
                        ?
                        <h1 className={reverseBg ? `${className}__title2` : `${className}__title`}>
                            <span className="title__first">{title.first}</span>
                            {title.second}
                    </h1>
                        :
                    <h1 className={reverseBg ? `${className}__title2` : `${className}__title`}>{title}</h1>}
                    {description ?
                        <h6 className={`${className}__description`}>{description}</h6>
                        : null
                    }
                    <MainButton btnText={btn.btnText} to={btn.to} classN={btn.classN} style2={style2}/>
                </div>
            </div>
            {arrowDown ? <ArrowsDown/> : null}
        </div>
    );
};

export default Section__Title;
