import React from 'react';
import {Link as LinkR} from "react-router-dom";
import './MainButton.scss'
import {IoArrowForwardCircleSharp} from 'react-icons/io5';

const MainButton = ({btnText, classN, to, style2, onClick, icon,}) => {
    return (
        <button className={style2 ? style2 : "btn__wrap"}>
            <LinkR className={classN} to={to ? to : ""}>{btnText}</LinkR>
            {icon ? <IoArrowForwardCircleSharp style={{color: "#2EA3E6", width: "25px", height: "25px"}}/> : null}
        </button>
    );
};

export default MainButton;
