import React from 'react';
import './Contacts.scss';
import {useSelector} from "react-redux";
import Content from "../../Content/Content";
import FormContacts from "../../components/Form/FormContacts";
import SectionContacts from "../../components/Sections/Section__Contacts/Section__Contacts";


const Contacts = () => {
    const lng = useSelector(state => state.language.language);
    return (
        <div className={'section__contacts__wrap'} >
            <SectionContacts contacts={Content[lng].contacts}/>
            <FormContacts form={Content[lng].form}/>
        </div>
    );
};

export default Contacts;