import React from 'react';
import { Button } from '@material-ui/core';
import { useFormikContext } from 'formik';

const ButtonWrapper = ({
                           children,
                           className,
                           ...otherProps
                       }) => {
    const { submitForm } = useFormikContext();

    const handleSubmit = () => {
        submitForm();
    }

    const configButton = {
        variant: 'contained',
        color: 'primary',
        onClick: handleSubmit
    }

    return (
        <Button
            {...configButton}
            className={className}
        >
            {children}
        </Button>
    );
};

export default ButtonWrapper;